
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
}
[v-cloak] {
  display: none;
}

#play-area {
  position: relative;
  max-height: 100vh;
  min-height: 700px;
  overflow: hidden;
}
#photo {
  display: block;
  border-right: none;
  cursor: -webkit-grab;
  cursor: grab;
}
#photo:active {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}

#character-picker {
  position: absolute;
  height: 50px;
  width: 50px;
  background: rgba(186, 218, 255, 0.4);
  border: 2px solid white;
  -webkit-transition: all 250ms cubic-bezier(0.2, 0.24, 0, 1.3);
  transition: all 250ms cubic-bezier(0.2, 0.24, 0, 1.3);
}
#picker-closer {
  top: -15px;
  right: -15px;
  background: #b10f0f;
  border: 2px solid white;
  border-radius: 50%;
  color: white;
  font-size: 11pt;
  font-weight: 700;
  line-height: 0;
  height: 25px;
  width: 25px;
}
.selection-icon {
  cursor: pointer;
  position: absolute;
  width: 50px;
}
.selection-icon:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  -webkit-box-shadow: 0px 0px 10px rgba(186, 218, 255, 0.4);
          box-shadow: 0px 0px 10px rgba(186, 218, 255, 0.4);
}
#wally {
  top: -65px;
}
#wenda {
  left: -65px;
}
#woof {
  right: -65px;
}
#wbeard {
  left: -35px;
  bottom: -65px;
}
#odlaw {
  right: -35px;
  bottom: -65px;
}

#sidebar {
  display: grid;
  grid-template-rows: 1fr auto auto;
  grid-gap: 1em;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: relative;
  background: #2c3e50;
  border-left: 2px solid white;
  color: white;
  font-weight: 700;
  text-align: center;
  padding-top: 1em;
  min-height: 700px;
  z-index: 10;
}
#sidebar ul {
  list-style-type: none;
}
#character-status {
  margin-bottom: 1em;
}
#character-status li {
  margin: 1em 0;
}
#character-status p { 
  border-radius: 0.25em;
}
.portrait {
  border-radius: 50%;
  -webkit-box-shadow: 0px 0px 10px #00000080;
          box-shadow: 0px 0px 10px #00000080;
}
.status-icon {
  border: 5px solid;
  width: 60px;
}
.found {
  border-color: #0a8b4f;
}
.not-found {
  border-color: #8b0a24;
}

#time {
  font-size: 1.5em;
}

#show-board {
  background: white;
  border: none;
  color: #2c3e50;
  cursor: pointer;
  padding: 1em 0.5em;
  font-weight: 700;
  font-size: 1em;
}
#score-board {
  position: absolute;
  right: 100%;
  bottom: 0;
  background: #2c3e50;
  border: 2px solid white;
  border-radius: 0.25em;
  cursor: pointer;
  margin: 0 1em 1em 0;
  width: 175px;
  z-index: -10;
}
#score-board li {
  padding: 0.5em 0.5em;
  font-size: 1.1em;
}
#score-board li:nth-child(even) {
  background: #3f5973;
}
.score {
  display: inline-block;
  margin-top: 0.5em;
  background: white;
  border-radius: 0.25em;
  padding: 0.2em 0.5em;
  color: #2c3e50;
}
#score-board li:first-child .score {
  border: 5px ridge rgb(112, 240, 229);
}
.slide-enter-active,
.slide-leave-active {
  -webkit-transition: all 150ms ease-out;
  transition: all 150ms ease-out;
}
.slide-enter,
.slide-leave-to {
  -webkit-transform: translateX(20%);
          transform: translateX(20%);
  opacity: 0;
}
